<template>
  <div>
    <ul>
      <li v-for="(item) in arr" :key="item.id">
        <h3>{{item.ip}}</h3>
      </li>
    </ul>
  </div>
</template>
<script>
import { requestIndex } from '../utils/request';
export default {
  components: {},
  data() {
    return {
      arr:[]
    };
  },
  methods: {
   
  },
  mounted(){
   requestIndex().then(res=>{
    //  console.log(res);
     this.arr=res.data.data
   })
  }
};
</script>
<style scoped>
</style>


